@import url("https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  line-height: 1;
  font-family: "Lato", sans-serif;
}

.title {
  text-align: left;
  padding: 20px 10px 20px 50px;
  border-bottom: 1px solid #000000;
  font-size: 30px;
}

.sub-title {
  font-size: 20px;
  padding: 10px 0;
  position: relative;
  display: flex;
  align-items: center;
  &:before {
    content: "";
    position: relative;
    width: 50px;
    height: 50px;
    margin-right: 10px;
    background: url("officer.svg");
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }
}

.container {
  background-image: linear-gradient(-225deg, #e3fdf5 0%, #ffe6fa 100%);
  min-height: 100vh;
}
.wrapper {
  display: flex;
  flex-wrap: wrap;
}

.users-container,
.date-container {
  padding: 50px;
  @media (max-width: 768px) {
    padding: 30px;
  }
}
.users-container {
  width: calc(100% - 400px);
  min-height: calc(100vh - 70px);
  border-right: 1px solid #000000;
  @media (max-width: 1380px) {
    width: calc(100% - 300px);
  }
  @media (max-width: 768px) {
    width: 100%;
    border-right: none;
    border-top: 1px solid #000000;
  }
}
.users-container-top {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}
.users-list {
  column-count: 3;
  @media (max-width: 1380px) {
    column-count: 2;
  }
  @media (max-width: 768px) {
    column-count: 1;
  }
}
.users-list-item {
  width: 100%;
  position: relative;
  padding: 0 0 60px 40px;
  float: left;
  break-inside: avoid;
}

.users-list-letter {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  text-align: center;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 700;
}

.user {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.user-input {
  margin-right: 10px;
  cursor: pointer;
}
.user-name {
  font-size: 15px;
  font-weight: 400;
}

.date-container {
  width: 400px;
  height: 100%;
  position: sticky;
  top: 0;
  @media (max-width: 1380px) {
    width: 300px;
  }
  @media (max-width: 768px) {
    position: relative;
    width: 100%;
    order: -1;
  }
  .sub-title {
    margin-bottom: 50px;
    &:before {
      background: url("cake.svg");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
}

.date-list-item {
  margin-bottom: 60px;
}

.date-month {
  font-size: 15px;
  font-weight: 700;
  margin-bottom: 10px;
  text-transform: uppercase;
}

.date-users {
  list-style-type: disc;
}

.date-users-item {
  margin-bottom: 10px;
  font-size: 15px;
}

.date-user-birthday {
  margin-top: 5px;
}

.button-clear-all {
  padding: 5px 30px;
  border-bottom: 1px solid #000000;
  font-size: 15px;
  cursor: pointer;
}

.users-notfound {
  font-size: 15px;
}

.text-error {
  padding: 50px;
  color: #ff5722;
  font-size: 30px;
  line-height: 1.3;
  span {
    text-transform: uppercase;
    font-weight: 700;
  }
}

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.75);
}

.spinner-in {
  width: 70px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 12;
  &.line {
    position: fixed;
  }
  & > div {
    width: 18px;
    height: 18px;
    background-color: #000000;
    border-radius: 100%;
    display: inline-block;
    animation: sk-bouncedelay 1.4s infinite ease-in-out both;
    &:nth-child(1) {
      animation-delay: -0.32s;
    }
    &:nth-child(2) {
      animation-delay: -0.16s;
      display: block;
    }
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
  }
  40% {
    transform: scale(1);
  }
}
